import { mapState, mapActions } from 'vuex'
import { dealLogin } from '@/common/js/common'
export default {
	data() {
		return {
			active: false
		};
	},
	onLoad() {
		this._reqGetUserInfo()
	},
	onShow() {
		// setTimeout(() => {
		this.active = true;
		// }, 500);
	},
	onHide() {
		this.active = false;
	},
	methods: {
		...mapActions(['reqGetUserInfo']),
		_reqGetUserInfo () {
			if(!dealLogin()) {
				return;
			}
			this.reqGetUserInfo({
				token: uni.getStorageSync('token')
			})
		},
		goToPage(url) {
			if(!dealLogin()) {
				return;
			}
			if (!url) return;
			uni.navigateTo({
				url
			});
		}
	},
	components: {
		// homeTemp
	}
};
